<template>
<div>
  <modal ref="dlg" :closeable="false">
    <div class="header">Subscribe Products</div>
    <div class="content">
      <p>Do you want to Subscribe Products?</p>
    </div>
    <div class="actions">

      <button class="ui negative button" @click="close()">Cancel</button>
    </div>
  </modal>
  <div class="title centertext">
    Log-in to your account
  </div>
  <message ref="msg"></message>
  <div class="ui middle aligned center aligned grid login">
    <div class="column">

      <form class="ui large attached form" :class="{'error': errors.any(), 'loading': isBusy }">
        <div class="ui error message">
          <div class="header">Validation failed</div>
          <p>{{ errors.first('Login name') }}</p>
          <p>{{ errors.first('Password') }}</p>
        </div>

        <div class="ui segment">
          <div class="field" :class="{'error': errors.has('Login name') }">
            <label>Login name</label>
            <div class="ui left icon input">
              <i class="user icon"></i>
              <input type="text" name="Login name" placeholder="Login name" autofocus v-model="loginname" v-validate data-rules="required|min:6|max:20">
            </div>
          </div>
          <div class="field" :class="{'error': errors.has('Password') }">
            <label>Password</label>
            <div class="ui left icon input">
              <i class="lock icon"></i>
              <input type="password" name="Password" placeholder="Password" v-model="password" v-validate data-rules="required|min:6|max:15">
            </div>
          </div>
          <button type="submit" class="ui fluid large blue submit button" @click.prevent="onLogin()">Login</button>
        </div>


        <div class="ui bottom attached center aligned message segment">
          New to Restock? <a @click="$router.push('signup')">Sign Up</a>
        </div>
      </form>


    </div>
  </div>
</div>
</template>

<script>
'use strict';

export default {

  data() {
    return {
      loginname: '',
      password: '',
      isBusy: false,
    }
  },
  methods: {
    close() {
      this.$refs.dlg.close()
    },
    onLogin() {
      //  this.$refs.dlg.open()

      this.$validator.validateAll()
      if (!this.errors.any()) {
        this.isBusy = true;
        let params = {
          method: 'user.login',
          args: {
            loginname: this.loginname.trim(),
            password: this.password.trim()
          }
        };

        this.$server.rpc(params).then((data) => {
          this.$login(data.id_token);
          this.$root.$emit('onLogin');
          this.isBusy = false
        }).catch((error) => {
          console.log(error)
          this.isBusy = false
          this.$refs.msg.setType('error')
          this.$refs.msg.setText(error.toString())
          this.$refs.msg.show()
        });
        params = null;

      } //if
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login {
  height: 75vh;
  margin: auto;
}

.segment {
  text-align: left;
  margin: 0;
}

#contents .column {
  padding-right: 2rem !important;
}

h1,
h3 {
  color: white;
  margin: 0;
}

h1 {
  font-size: 3em;
}

@media all and (min-width: 0) and (max-width: 480px) {
  .login {
    width: 92%;
  }
}

@media all and (min-width: 481px) and (max-width: 840px) {
  .login {
    width: 70%;
  }
}

@media all and (min-width: 841px) and (max-width: 1280px) {
  .login {
    width: 40%;
  }
}

@media all and (min-width: 1281px) and (max-width: 1600px) {
  .login {
    width: 35%;
  }
}
</style>



